<template>
        <!-- FORM START -->
        <form class="form-horizontal">
         <div class="form-group">
          <label class="col-sm-3 control-label">
          
          </label>
          <div class="col-sm-9">
            <input type="text"  placeholder=" " name="newMobile" v-model="credentials.mobile" required class="form-control">
          </div>
        </div>

        <div class="form-group">
          <label class="col-sm-3 control-label">
          
          </label>
          <div class="col-sm-4">
            <input type="text"  placeholder=" " name="authCode" v-model="credentials.authCode" required class="form-control">
          </div>
          <div class="col-sm-5">
            <count-down @click.prevent="validateMobile" :disabled="notMobile"></count-down>
          </div>
        </div>

        <div class="form-group">
          <label class="col-sm-3 control-label">
          
          </label>
          <div class="col-sm-9">
             <input type="text"  class="form-control" placeholder=" " name="password" v-model="credentials.password" required>
          </div>
        </div>

                <div class="form-group">
          <label class="col-sm-3 control-label">
          
          </label>
          <div class="col-sm-9">
             <input type="text"  class="form-control" name="password_confirmation" placeholder=" " v-model="credentials.rePassword"  required>
          </div>
        </div>
        <button type="submit" class="btn btn-light-blue btn-lg btn-block margin-bottom-10" @click.stop.prevent="signIn"><i class="fa fa-sign-in"></i> </button>
         </form>
          <alert
          :show.sync="showInfo"
          :duration="3000"
          type="info"
          dismissable>
          <p><i class="fa fa-warning"></i> {{info}}</p>
        </alert>

</template>

<script>
var alert = require('vue-strap/dist/vue-strap.min').alert
import * as utils from '../utils'
import * as validate from '../validator'
import countDown from '../components/CountDownBtn.vue'
var swal = require('sweetalert/lib/sweetalert')
export default {
  replace: true,
  data () {
    return {
      credentials: {
        mobile: '',
        password: '',
        rePassword: '',
        authCode: ''
      },
      showInfo: false,
      error: '',
      info: ''
    }
  },
  components: {
    alert,
    countDown,
    swal
  },
  methods: {
    signIn () {
      var data = {
        mobile: this.credentials.mobile,
        password: this.credentials.password,
        password_confirmation: this.credentials.rePassword,
        authCode: this.credentials.authCode
      }
/**
 * findpass
 */
      this.$http.post('auth/findpass', data).then(function (response) {
        swal({
          title: '!',
          text: '',
          type: 'success',
          closeOnConfirm: false,
          confirmButtonText: '',
          confirmLoadingButtonColor: '#DD6B55'
        }, function () {
          window.location.href = '/login'
        })
      }, function (response) {
        this.$set('error', utils.findFirst(response.data))
        swal('!', this.error, 'error')
      })
    },
    validateMobile () {
      var data = {
        mobile: this.credentials.mobile
      }
      this.$http.post('service/sendSms', data).then(function (response) {
        this.showInfo = true
        this.info = ','
      }, function (response) {
        this.$set('error', utils.findFirst(response.data))
        swal('!', this.error, 'error')
      })
    }
  },
  computed: {
    notMobile: function () {
      return !validate.isMobile(this.credentials.mobile)
    }
  }
}
</script>




